import Vue from 'vue'
import App from '@/App'
import Router from 'vue-router'
import 'html5-reset/assets/css/reset.css'


import applySuccessList from '@/view/crowdTest/apply-success-list.vue'; //申请成功名单


import moment from 'moment'
Vue.filter('dateFormat', function (value, type) { //时间格式化过滤
  return moment(value).format(type);
});


Vue.use(Router)

const router = new Router({
  // mode: 'history',
  // base: '/apply-success-list.html',
  routes: [{
    path: '/',
    name: 'applySuccessList',
    component: applySuccessList
  }]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



